<template>
  <li>
    <span>案件来源：</span>
    <el-select id="u827_input" v-model="industry" style="width: 100px;">
      <el-option v-for="(i, index) in item" :key="index" :label="i.name" :value="i.id">
      </el-option>
    </el-select>
  </li>
</template>

<script>
export default {
  data () {
    return {
      industry: -1,
      item: []
    }
  },
  mounted () {
    this.init()
  },
  methods: {
    init () {
      var self = this
      this.$http.get({
        api: 'A_S_CASE_CODE',
        params: {
          id: 'CASE_SOURCE'
        },
        callback: (res) => {
          res.unshift({
            id: -1,
            name: '全部'
          })
          self.item = res
        }
      })
    }
  },
  watch: {
    'industry' (val, oldVal) {
      this.$emit('setParams', val)
    }
  }
}

</script>



